import { Pagination } from "@mui/material";
import axios from "axios";
import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { productListAction } from "../Redux/Actions/productAction";

const PaginationTo = () => {
    const [count,setCount] = useState();
    const [page,setPage] = useState(1);
    const dispatch = useDispatch()
    const [cou,setCou] = useState(0)
    useEffect(()=>async()=>{
       const data = await axios.get("http://localhost:5000/getProductCount")
        setCou(data?.data?.message)
       const number = data?.data?.message/4;
       setCount(Math.ceil(number))
    },[])
    return (
        <>
            <div class="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
                <div class=" sm:flex sm:flex-1 sm:items-center sm:justify-between">
                    <div className="mb-2">
                        <p class="text-sm text-gray-700">
                            Showing{" "}
                            <span class="font-medium">{((page-1)*4)+1}</span>
                            {" "} to  {" "}
                            <span class="font-medium">{page*4}</span>
                            of{" "}
                            <span class="font-medium">{cou}</span>
                           {" "} results
                        </p>
                    </div>
                    <div>
                        <nav class=" inline-flex rounded-md shadow-sm" aria-label="Pagination">
                            <Pagination onChange={(e,value)=>{dispatch(productListAction(4,value));setPage(value)}} count={count} variant="outlined" shape="rounded" />
                        </nav>
                    </div>
                </div>
            </div>

        </>
    )
}

export default PaginationTo;