import { PRODUCT_LIST_FAIL, PRODUCT_LIST_SUCCESS, PRODUCT_LIST_REQUEST, PRODUCT_DETAIL_FAIL, PRODUCT_DETAIL_SUCCESS, PRODUCT_DETAIL_REQUEST } from "../contants"
import axios from "axios";

export const productListAction = (limit,offset) => async (dispatch) => {
    try {
        const config = {
            headers: {
                "Content-Type": "application/json"
            }
        }
        dispatch({ type: PRODUCT_LIST_REQUEST })
        const { data } = await axios.post("/getProductList",{limit,offset},config)
        dispatch({ type: PRODUCT_LIST_SUCCESS, payload: data })
    }
    catch (err) {
        dispatch({ type: PRODUCT_LIST_FAIL, payload: err.response && err.response.data.message ? err.response.data.message : err.message })
    }
}

export const productDetailAction = (id) => async (dispatch) => {
    try {
        dispatch({ type: PRODUCT_DETAIL_REQUEST })
        const { data } = await axios.get(`/product/${id}`)
        dispatch({ type: PRODUCT_DETAIL_SUCCESS, payload: data })
    }
    catch (err) {
        dispatch({ type: PRODUCT_DETAIL_FAIL, payload: err.response && err.response.data.message ? err.response.data.message : err.message })
    }
}
